.card, .loan-offer, .deck, .discard {
  background-color: orange;
  border-radius: 10%;
  -webkit-box-shadow: 2px 2px 5px black;
          box-shadow: 2px 2px 5px black;
  height: 80px;
  width: 50px;
  margin: 3px;
  -webkit-transition: 0.5s;
  -o-transition: 0.5s;
  transition: 0.5s;
}
.card.selected {
  border: 1px solid red;
  -webkit-box-shadow: 2px 2px 5px red;
          box-shadow: 2px 2px 5px red;
}
.building .card {
  border: 1px solid green;
  -webkit-box-shadow: 2px 2px 5px green;
          box-shadow: 2px 2px 5px green;
}
.card .name {
  font-size: 0.5em;
}
.card .cost {
  display: -ms-flexbox;
  display: flex;
  font-size: 0.6em;
  -ms-flex-pack: end;
      justify-content: flex-end;
}
.card .cost div{
  margin: 2px;
}
.card .provides {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column-reverse;
      flex-direction: column-reverse;
  font-size: 0.8em;
  height: 65%;
}

.loan-offer {
    background-color: red;
    color: white;
    font-size: 0.6em;
}
.deck, .discard {
    background-color: darkgreen;
    color: white;
    font-size: 1.5em;
    line-height: 80px;
    vertical-align: middle;
}
.discard {
    background-color: darkgrey;
}



